/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react'
import {Navigate, Outlet, Route, Routes, useNavigate} from 'react-router-dom'
import {Error500} from './components/Error500'
import {Error404} from './components/Error404'
import {toAbsoluteUrl} from '../../../_metronic/helpers'

const ErrorLayout: React.FC = () => {
  const navigate = useNavigate()
  const redirectToDashboard = () => {
    navigate('/')
  }

  return (
    <>
    <div className='d-flex flex-column flex-root'>
      <div
        className='d-flex flex-column flex-column-fluid bgi-position-y-bottom position-x-center bgi-no-repeat bgi-size-contain bgi-attachment-fixed'
        style={{backgroundImage: `url('${toAbsoluteUrl('/media/illustrations/progress-hd.png')}')`}}
      >
        <div className='d-flex flex-column flex-column-fluid text-center p-10 py-lg-20'>
          <a href='/dashboard' className='mb-10 pt-lg-20'>
            <img
              alt='Logo'
              src={toAbsoluteUrl('/musician/new.jpeg')}
              className='h-50px mb-5'
            />
          </a>
          <div className='pt-lg-10'>

            <Outlet />

            <div className='text-center'>
              <a href='/explore' className='btn btn-lg btn-primary fw-bolder'>
                Go to Explore
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
    </>
  )
}

const ErrorsPage = () => (
  <Routes>
    <Route element={<ErrorLayout />}>
      <Route path='/404' element={<Error404 />} />
      <Route path='/500' element={<Error500 />} />
      <Route index element={<Error404 />} />
    </Route>
  </Routes>
)

export {ErrorsPage}
