import { useEffect, useState } from "react"
import { FormattedMessage, useIntl } from "react-intl"
import { shallowEqual, useSelector } from "react-redux"
import CircularProgress from "@mui/material/CircularProgress"
import { RootState } from "setup"
import { AlertDanger, AlertSuccess } from "../alerts/Alerts"
import { getArtistsData } from "../artistWizard/api"
import { getMusicianMeta, updateMusicianMeta } from "./api"
import { toAbsoluteUrl } from "_metronic/helpers"
import { Navigate, Route, Routes } from "react-router-dom";

export const MusicianMetadataManagement = () => {
    const [loading, setLoading] = useState<boolean>(false)
    const [fetchingData, setFetchingData] = useState<boolean>(true)
    const [successMessage, setSuccessMessage] = useState<string>("")
    const [errorMessage, setErrorMessage] = useState<string>("")
    const [checked, setChecked] = useState<boolean>(false)

    const getCurrentDataTime = () => {
        let now = new Date();
        let utcString = now.toISOString().substring(0,19);
        let year = now.getFullYear();
        let month = now.getMonth() + 1;
        let day = now.getDate();
        
        var localDate = year + "-" +
                          (month < 10 ? "0" + month.toString() : month) + "-" +
                          (day < 10 ? "0" + day.toString() : day);
    
        return localDate
    }

    const [artistData, setArtistData] = useState<Array<any>>([])
    const [collectionData, setCollectionData] = useState<Array<any>>([])
    const [musicianDescription, setMusicianDescription] = useState<string>("")
    const [bannerVideoName, setBannerVideoName] = useState<string>("")
    const [musicianVideoName, setmusicianVideoName] = useState<string>("")
    const [youtubeVideoURL, setYoutubeVideoURL] = useState<string>("")
    const [selectedArtist, setSelectedArtist] = useState<string>("")
    const [selectedCollection, setSelectedCollection] = useState<string>("")

    const [topAlbum1Image, setTopAlbum1Image] = useState<any>("")
    const [topAlbum1ImageName, setTopAlbum1ImageName] = useState<any>("")
    const [topAlbum1Name, setTopAlbum1Name] = useState<any>("")
    const [topAlbum1ReleaseDate, setTopAlbum1ReleaseDate] = useState<any>("")
    const [topAlbum1Genre, setTopAlbum1Genre] = useState<any>("")
    const [topAlbum1ServiceURL, setTopAlbum1ServiceURL] = useState<any>("")

    const [topAlbum2Image, setTopAlbum2Image] = useState<any>("")
    const [topAlbum2ImageName, setTopAlbum2ImageName] = useState<any>("")
    const [topAlbum2Name, setTopAlbum2Name] = useState<any>("")
    const [topAlbum2ReleaseDate, setTopAlbum2ReleaseDate] = useState<any>("")
    const [topAlbum2Genre, setTopAlbum2Genre] = useState<any>("")
    const [topAlbum2ServiceURL, setTopAlbum2ServiceURL] = useState<any>("")

    const [bannerVideo, setBannerVideo] = useState<any>("")
    const [musicianVideo, setmusicianVideo] = useState<any>("")



    const intl = useIntl()

    const { isAdmin }: any = useSelector<RootState>(
        ({auth}) => ({
            isAdmin: auth.user?.isAdmin
        }), shallowEqual
    )

    useEffect(() => {
        getMusicianMeta()
         .then(async (response) => {
            setArtistData(response.data.artist_data)
            setCollectionData(response.data.collectionData)
            setBannerVideoName(response.data.banner_video_name)
            setmusicianVideoName(response.data.musician_video_name)
            setSelectedCollection(response.data.featured_collection)
            setSelectedArtist(response.data.featured_artist)
            setYoutubeVideoURL(response.data.youtube_video_url)

            setTopAlbum1ImageName(response.data.top_album_1_data.top_album_1_image_name)
            setTopAlbum1Name(response.data.top_album_1_data.top_album_1_name)
            setTopAlbum1ReleaseDate(response.data.top_album_1_data.top_album_1_release_date)
            setTopAlbum1Genre(response.data.top_album_1_data.top_album_1_genre)
            setTopAlbum1ServiceURL(response.data.top_album_1_data.top_album_1_service_url)

            setTopAlbum2ImageName(response.data.top_album_2_data.top_album_2_image_name)
            setTopAlbum2Name(response.data.top_album_2_data.top_album_2_name)
            setTopAlbum2ReleaseDate(response.data.top_album_2_data.top_album_2_release_date)
            setTopAlbum2Genre(response.data.top_album_2_data.top_album_2_genre)
            setTopAlbum2ServiceURL(response.data.top_album_2_data.top_album_2_service_url)

            let musicianDescriptArr = JSON.parse(response.data.musician_description.replace(/&quot;/g, '"'))
            let musicianDescriptStr = ""

            await musicianDescriptArr.map((lines : any) => {
                musicianDescriptStr += lines + '\n'
            })

            setMusicianDescription(musicianDescriptStr)
            setFetchingData(false)
         })
    }, [])

    if(successMessage !== "") {
        setTimeout(() => {
          setChecked(false);
          setTimeout(() => {
            setSuccessMessage("");
          }, 200);
        }, 5000);
    }
    
    if(errorMessage !== "") {
        setTimeout(() => {
            setChecked(false);
            setTimeout(() => {
            setErrorMessage("");
            }, 200);
        }, 5000);
    }

    // const handleBannerVideoURLChange = (event: any) => {
    //     event.preventDefault()

    //     setBannerVideoURL(event.target.value)
    // }

    // const handleMusicianVideoURLChange = (event: any) => {
    //     event.preventDefault()

    //     setmusicianVideoURL(event.target.value)
    // }

    const handleBannerVideoChange = (e: any) => {
        setBannerVideo(e.target.files[0])
    };

    const handleMusicianVideoChange = (e: any) => {
        setmusicianVideo(e.target.files[0])
    };

    const handleYoutubeVideoURLChange = (event: any) => {
        event.preventDefault()

        setYoutubeVideoURL(event.target.value)
    }

    const handleMusicianDescriptionChange = (event: any) => {
        event.preventDefault()

        setMusicianDescription(event.target.value)
    }

    const handleCollectionChange = (event: any) => {
        event.preventDefault()

        setSelectedCollection(event.target.value)
    }

    const handleArtistChange = (event: any) => {
        event.preventDefault()

        setSelectedArtist(event.target.value)
    }

    const handleTopAlbum1NameChange = (event: any) => {
        event.preventDefault()
        setTopAlbum1Name(event.target.value)
    }

    const handleTopAlbum2NameChange = (event: any) => {
        event.preventDefault()
        setTopAlbum2Name(event.target.value)
    }

    const handleTopAlbum1ImageChange = (e: any) => {
        setTopAlbum1Image(e.target.files[0])
    };
    
    const handleTopAlbum2ImageChange = (e: any) => {
        setTopAlbum2Image(e.target.files[0])
    };

    const handleTopAlbum1ReleaseDate = (event: any) => {
        event.preventDefault()
        setTopAlbum1ReleaseDate(event.target.value)
    }

    const handleTopAlbum2ReleaseDate = (event: any) => {
        event.preventDefault()
        setTopAlbum2ReleaseDate(event.target.value)
    }

    const handleTopAlbum1GenreChange = (event: any) => {
        event.preventDefault()
        setTopAlbum1Genre(event.target.value)
    }

    const handleTopAlbum2GenreChange = (event: any) => {
        event.preventDefault()
        setTopAlbum2Genre(event.target.value)
    }

    const handleTopAlbum1ServiceURL = (event: any) => {
        event.preventDefault()
        setTopAlbum1ServiceURL(event.target.value)
    }

    const handleTopAlbum2ServiceURL = (event: any) => {
        event.preventDefault()
        setTopAlbum2ServiceURL(event.target.value)
    }

    const handleSubmit = () => {
        setLoading(true)

        let musicianDescArr = musicianDescription.split('\n')

        let formData = new FormData()
        formData.append('bannerVideo', bannerVideo)
        formData.append('musicianVideo', musicianVideo)
        formData.append('youtubeVideoURL', youtubeVideoURL)
        formData.append('artistID', selectedArtist)
        formData.append('collectionID', selectedCollection)
        formData.append('musicianDescription', JSON.stringify(musicianDescArr))

        formData.append('topAlbum1Image', topAlbum1Image)
        formData.append('topAlbum1Name', topAlbum1Name)
        formData.append('topAlbum1Genre', topAlbum1Genre)
        formData.append('topAlbum1ReleaseDate', topAlbum1ReleaseDate)
        formData.append('topAlbum1ServiceURL', topAlbum1ServiceURL)
        formData.append('topAlbum2Image', topAlbum2Image)
        formData.append('topAlbum2Name', topAlbum2Name)
        formData.append('topAlbum2Genre', topAlbum2Genre)
        formData.append('topAlbum2ReleaseDate', topAlbum2ReleaseDate)
        formData.append('topAlbum2ServiceURL', topAlbum2ServiceURL)

        updateMusicianMeta(formData)
        .then((response) => {
            if(response.data.success) {
                setChecked(true)
                setSuccessMessage(response.data.message)
                window.scrollTo(0,0)
                setLoading(false)
            } else {
                setChecked(true)
                setSuccessMessage(intl.formatMessage({ id: 'MUSICIAN_META.UPDATE_FAILED' }))
                window.scrollTo(0,0)
                setLoading(false)
            }
        })
        .catch(() => {
            setChecked(true)
            setSuccessMessage(intl.formatMessage({ id: 'MUSICIAN_META.UPDATE_FAILED' }))
            window.scrollTo(0,0)
            setLoading(false)
        })
    }

    return (
        <>
            {isAdmin == '1' &&
                <>
                {successMessage !== "" ? (
                    <AlertSuccess message={successMessage} checked={checked} />
                ) : null }
        
                {errorMessage !== "" ? (
                    <AlertDanger message={errorMessage} checked={checked} />
                ) : null }
        
                {!fetchingData && 
                    <>
                        <div className='card'>
                            <div className='card-body'>
                
                                <div
                                    className='stepper stepper-links d-flex flex-column pt-15'
                                    id='kt_create_account_stepper'
                                >
                                    <div className='stepper-nav mb-5'>
                                        <div className='stepper-item current'>
                                            <h3 className='stepper-title'><FormattedMessage id="MUSICIAN_META.TITLE" /></h3>
                                        </div>
                                    </div>
                                </div>
                                <div className='mx-auto mw-600px w-100 pt-15 pb-10'>
                                    <div className='w-100'>
                                        
                                        <div className='fv-row mb-10'>
                                            <label className='form-label required'>
                                            <FormattedMessage id="MUSICIAN_META.FEATURED_COLLECTION" />
                                            </label>

                                            <select
                                                value={selectedCollection}
                                                className='form-select form-select-lg form-select-solid'
                                                name='artistName'
                                                onChange={(e) => handleCollectionChange(e)}
                                            >
                                                <option value="">
                                                    {/* <FormattedMessage id="MUSICIAN_META.SELECT_COLLECTION" /> */}
                                                    {intl.formatMessage({ id: 'MUSICIAN_META.SELECT_COLLECTION' })}
                                                </option>

                                                {collectionData.map((data: any) => (
                                                    <option key={data.id} value={data.id}>{data.collection_name}</option>
                                                ))}
                                            </select>
                                        </div>
                                        
                                        <div className='fv-row mb-10'>
                                            <label className='form-label required'>
                                                <FormattedMessage id="MUSICIAN_META.FEATURED_ARTIST" />
                                            </label>

                                            <select
                                                value={selectedArtist}
                                                className='form-select form-select-lg form-select-solid'
                                                name='artistName'
                                                onChange={(e) => handleArtistChange(e)}
                                            >
                                                {/* <option value=''><FormattedMessage id="MUSICIAN_META.SELECT_ARTIST" /></option> */}
                                                <option value="">{intl.formatMessage({ id: 'MUSICIAN_META.SELECT_ARTIST' })}</option>

                                                {artistData.map((data: any) => (
                                                    <option key={data.id} value={data.id}>{data.artist_name}</option>
                                                ))}
                                            </select>
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label required mb-3'>
                                            Banner Video
                                            <span className='ms-1'>(Recommended size: Below 5000 KB)</span>
                                            </label>

                                            {bannerVideo !== "" &&
                                                <div className="w-70">
                                                   <video controls controlsList="nodownload" playsInline style={{ width: '100%', height: '400px', objectFit: 'cover' }}>
                                                        {/* <source src="https://api.royal.io/assets/183636fa-2cee-4208-93ae-d87336762135" type="video/mp4" /> */}
                                                        <source src={URL.createObjectURL(bannerVideo)} type="video/mp4" />
                                                    </video>
                                                </div>
                                            }

                                            {bannerVideo == "" && bannerVideoName && bannerVideoName !== "" &&
                                                 <div className="w-70">
                                                    <video controls controlsList="nodownload" playsInline style={{ width: '100%', height: '400px', objectFit: 'cover' }}>
                                                        {/* <source src="https://api.royal.io/assets/183636fa-2cee-4208-93ae-d87336762135" type="video/mp4" /> */}
                                                        <source 
                                                            src={`${process.env.REACT_APP_ASSET_URL}/${bannerVideoName}`} 
                                                            type="video/mp4" 
                                                        />
                                                    </video>
                                                </div>
                                            }

                                            <div>
                                            <input type="file" id="image1" accept="video/*" onChange={(e) => handleBannerVideoChange(e)} />
                                            </div>
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label required mb-3'>
                                            Dashboard Video
                                            <span className='ms-1'>(Recommended size: Below 5000 KB)</span>
                                            </label>

                                            {musicianVideo !== "" &&
                                                 <div className="w-70">
                                                    <video controls controlsList="nodownload" playsInline style={{ width: '100%', height: '400px', objectFit: 'cover' }}>
                                                        {/* <source src="https://api.royal.io/assets/183636fa-2cee-4208-93ae-d87336762135" type="video/mp4" /> */}
                                                        <source src={URL.createObjectURL(musicianVideo)} type="video/mp4" />
                                                    </video>
                                                </div>
                                            }

                                            {musicianVideo == "" && musicianVideoName && musicianVideoName !== "" &&
                                                 <div className="w-70">
                                                    <video controls controlsList="nodownload" playsInline style={{ width: '100%', height: '400px', objectFit: 'cover' }}>
                                                        {/* <source src="https://api.royal.io/assets/183636fa-2cee-4208-93ae-d87336762135" type="video/mp4" /> */}
                                                        <source 
                                                            src={`${process.env.REACT_APP_ASSET_URL}/${musicianVideoName}`} 
                                                            type="video/mp4" 
                                                        />
                                                    </video>
                                                </div>
                                            }

                                            <div>
                                            <input type="file" id="image1" accept="video/*" onChange={(e) => handleMusicianVideoChange(e)} />
                                            </div>
                                        </div>
                                        
                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 1 Image
                                                <span className='ms-1'>(Recommended size: 900 x 900)</span>
                                            </label>

                                            {topAlbum1Image === "" ? (
                                                <div>
                                                    <img src={`${process.env.REACT_APP_ASSET_URL}/${topAlbum1ImageName}`} className='w-300px mb-3' />
                                                </div>
                                            ): (
                                                <div>
                                                    <img src={URL.createObjectURL(topAlbum1Image)} className='w-300px mb-3' />
                                                </div>
                                            )}

                                            <div>
                                                <input type="file" id="image" accept="image/png, image/jpeg" onChange={(e) => handleTopAlbum1ImageChange(e)} />
                                            </div>
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 1 Name
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={topAlbum1Name}
                                                onChange={(event) => handleTopAlbum1NameChange(event)}
                                            />
                                        </div>  

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 1 Genre
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={topAlbum1Genre}
                                                onChange={(event) => handleTopAlbum1GenreChange(event)}
                                            />
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3 required'>
                                                Best Album 1 Release date
                                            </label>

                                            <input 
                                                type="date" 
                                                className='form-control form-control-lg form-control-solid'
                                                value={topAlbum1ReleaseDate != "" ? topAlbum1ReleaseDate : getCurrentDataTime()}
                                                min="2022-06-07" 
                                                max="2040-06-14"
                                                onChange={(event) => handleTopAlbum1ReleaseDate(event)}
                                            />
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 1 Service URL
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={topAlbum1ServiceURL}
                                                onChange={(event) => handleTopAlbum1ServiceURL(event)}
                                            />
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 2 Image
                                                <span className='ms-1'>(Recommended size: 900 x 900)</span>
                                            </label>

                                            {topAlbum2Image === "" ? (
                                                <div>
                                                    <img src={`${process.env.REACT_APP_ASSET_URL}/${topAlbum2ImageName}`} className='w-300px mb-3' />
                                                </div>
                                            ): (
                                                <div>
                                                    <img src={URL.createObjectURL(topAlbum2Image)} className='w-300px mb-3' />
                                                </div>
                                            )}

                                            <div>
                                                <input type="file" id="image" accept="image/png, image/jpeg" onChange={(e) => handleTopAlbum2ImageChange(e)} />
                                            </div>
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 2 Name
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={topAlbum2Name}
                                                onChange={(event) => handleTopAlbum2NameChange(event)}
                                            />
                                        </div>  

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 2 Genre
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={topAlbum2Genre}
                                                onChange={(event) => handleTopAlbum2GenreChange(event)}
                                            />
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3 required'>
                                                Best Album 2 Release date
                                            </label>

                                            <input 
                                                type="date" 
                                                className='form-control form-control-lg form-control-solid'
                                                value={topAlbum2ReleaseDate != "" ? topAlbum2ReleaseDate : getCurrentDataTime()}
                                                min="2022-06-07" 
                                                max="2040-06-14"
                                                onChange={(event) => handleTopAlbum2ReleaseDate(event)}
                                            />
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                Best Album 2 Service URL
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={topAlbum2ServiceURL}
                                                onChange={(event) => handleTopAlbum2ServiceURL(event)}
                                            />
                                        </div>

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                {/* <FormattedMessage id="CREATE_EDIT_ARTIST.ARTIST_COVER_IMAGE" />
                                                <span className='ms-1'>(Recommended size: 1500 x 300)</span> */}
                                                <FormattedMessage id="MUSICIAN_META.YOUTUBE_VIDEO_URL" />
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={youtubeVideoURL}
                                                onChange={(event) => handleYoutubeVideoURLChange(event)}
                                            />
                                        </div>
                
                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3 required'>
                                                {/* <FormattedMessage id="CREATE_EDIT_ARTIST.ABOUT_ARTIST" /> */}
                                                <FormattedMessage id="MUSICIAN_META.PLATFORM_DESCRIPTION" />
                                            </label>
                
                                            <textarea
                                                rows={5}
                                                className='form-control form-control-lg form-control-solid'
                                                value={musicianDescription}
                                                onChange={(event) => handleMusicianDescriptionChange(event)}
                                                // onKeyPress={(event) => handleEnterPress(event)}
                                            />
                                        </div>
                                        </div>
                                        <div className='d-flex flex-stack pt-15'>
                                        <div className='mr-2'>
                                        </div>
                
                                        <div>
                                            {loading && (
                                                <button type='submit' className='btn btn-lg btn-primary me-3'>
                                                    <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMITTING" />..
                                                    <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                                                </button>
                                            )
                                            }
                    
                                            {!loading && (
                                                <button 
                                                    className='btn btn-lg btn-primary me-3'
                                                    onClick={handleSubmit}
                                                >
                                                    <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMIT" />
                                                </button>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </>
                }
                {fetchingData && 
                    <div className='d-flex w-100 h-100'>
                        <div className='my-auto mx-auto'>
                            <div className='d-flex flex-column justify-content-center'>
                                <span className='text-primary fs-3'><FormattedMessage id="AUTH.REGISTER.LOADING" /></span> <br />
                                <span className='ms-4'>
                                    <CircularProgress />
                                </span>
                            </div>
                        </div>
                    </div>
                }
                </>
            } 

            {isAdmin == '0' && 
                <Routes>
                    <Route index element={<Navigate to={'/error/404'} /> }/>
                </Routes>
            }
        </>
    )
}