import { useState } from "react"
import { FormattedMessage } from "react-intl"



const customStyle = {
    borderColor: '#ca8837',
    color: '#ca8837',
    backgroundColor: 'transparent'
}


export const VideoSection = (props: any) => {
    const [style, changeStyle] = useState(customStyle)

    const handleMouseLeave = () => {
        changeStyle(customStyle);
    }

    const handleMouseEnter = () => {
        const newStyle = {
            borderColor: '#ca8837',
            color: '#ffffff',
            backgroundColor: '#ca8837'
        }
        changeStyle(newStyle)
    }

    return (
        <>
            <div id="video" style={{ background: '#000' }} className="vc_row wpb_row vc_row-fluid">
                <div className="wpb_column vc_column_container vc_col-sm-12">
                    <div className="vc_column-inner">
                        <div className="wpb_wrapper">
                            <div className="vc_row wpb_row vc_inner vc_row-fluid vc_custom_1505769992804">
                                <div style={{ background: '#e1e1e1', marginBottom: '75px' }} className="wpb_column vc_column_container vc_col-sm-12">
                                    <div style={{ marginBottom: '180px' }} className="vc_column-inner">
                                        <div className="wpb_wrapper">
                                            <h2 style={{ textAlign: 'center', color: '#000' }} className="vc_custom_heading vc_custom_1505770077799 fit_bg_text  fit_bg_center ">
                                                <FormattedMessage id="HOME_PAGE.LATEST_RELEASE" />
                                            </h2>
                                            <div className="wpb_video_widget wpb_content_element vc_clearfix vc_video-aspect-ratio-169 vc_video-el-width-80 vc_video-align-center" >
                                                <div className="wpb_wrapper">
                                                    <div className="wpb_video_wrapper">
                                                        {/* <iframe 
                                                            width="560" 
                                                            height="315" 
                                                            src="https://www.youtube.com/embed/o1Uo75TlYzc" 
                                                            title="YouTube video player" 
                                                            frameBorder="0" 
                                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                                                            allowFullScreen
                                                        ></iframe> */}
                                                        <iframe 
                                                            width="560" 
                                                            height="315" 
                                                            src={props.youtubeVideoURL} 
                                                            title="YouTube video player" 
                                                            frameBorder="0" 
                                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                                                            allowFullScreen
                                                        ></iframe>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}